#back-to-top {
  font-size: 20px;
  position: fixed;
  bottom: 50px;
  right: 50px;
  padding: 10px 15px;
  border-radius: 50%;
  border: none;
  color: #fff;
  background-color: #1b1b1b;
  cursor: pointer;
  z-index: 100;
  transition: 300ms;
  &:hover {
    transform: scale(1.2);
  }
}
